@import '../../../assets/css/_custom';
.ava {
  svg {
    path {
      stroke: var(--dark);
    }
  }
  background: #eee;
  box-shadow: rgb(45 45 45 / 5%) 0 2px 2px, rgb(49 49 49 / 5%) 0 4px 4px, rgb(42 42 42 / 5%) 0 8px 8px,
    rgb(32 32 32 / 5%) 0 16px 16px, rgb(49 49 49 / 5%) 0 32px 32px, rgb(35 35 35 / 5%) 0 64px 64px;
}

.list {
  height: calc(100vh - 235px);
  overflow: auto;
  @include scroll(6px, 6px, transparent);

}


.tabs {
  :global {
    .ant-list-item {
      padding: 0 12px;
    }
  }
}
